.products {
  padding-top: 120px;
  padding-bottom: 100px;
  .title {
    font-family: Titan One;
    font-weight: normal;
    font-size: 34px;
    line-height: 1.15;
    text-align: center;
    text-transform: uppercase;

    color: $accent-color;
  }
}

.secondary-title {
  font-family: Titan One;
  font-weight: normal;
  font-size: 18px;
  line-height: 1.17;
  text-align: center;
  text-transform: uppercase;

  color: $advanced-bg;
}
.products-list {
  &:first-child {
    background-color: #ffb8ca;
  }
  &:nth-child(2n) {
    background-color: #f0d1a5;
  }
  &:nth-child(3n) {
    background-color: #c2e297;
  }
}

.products-item {
  position: relative;
  width: 100%;
  padding: 160px 40px 40px;
  border-radius: 24px;
  &:not(:last-child) {
    margin-bottom: 130px;
  }
}

.terteary-title {
  font-family: Titan One;
  font-weight: normal;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  text-transform: uppercase;
  color: $main-color;
  margin-bottom: 30px;
}

.cards-dots {
  width: 26px;
  height: 6px;
  margin-bottom: 30px;
  background-image: url(../images/svg/products-dots.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.products-item-text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 1.52;
  text-align: center;
  color: $main-color;
  margin-bottom: 30px;
}

.products-button {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  text-align: center;
  align-self: center;
}
